.row {
  width: 100%;
  display: flex;
  justify-content: space-around;
  margin-bottom: 2rem;
}

.icon-container {
  display: flex;
  width: 25%;
  border: 1px solid #7f8fa6;
  border-radius: 1rem;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.my-stack {
  width: 100%;
  background-color: #f5f6fa;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding-top: 2rem;
}

.stack__img-icon {
  width: 25%;
  object-fit: cover;
}

.stack__fab-icon {
  display: inline-block;
  font-size: 10rem;
  color: red;
}

.icon-color-blue {
  color: blue;
}

.icon-color-green {
  color: #40ff00;
}

.icon-color-yellow {
  color: #ffff4d;
}

.icon-color-light-blue {
  color: #3399ff;
}

.stack-text {
  height: 8rem;
  padding: 1rem;
  font-size: 2rem;
  color: #47476b;
}
/*title*/
.medium-header-container {
  height: 4rem;
  display: flex;
  align-items: center;
}
.medium-header {
  height: 4rem;
  font-size: 3rem;
  color: #47476b;
  text-align: center;
}
