.navbar {
  background: linear-gradient(90deg, #f5f6fa 0%, #7f8fa6 100%);
  height: 8rem;
  display: flex;
  font-size: 1.5rem;
  padding-left: 1rem;
  align-items: center;
  justify-content: space-between;
}

.navbar-logo {
  color: #fff;
  margin-left: 3rem;
  text-decoration: none;
  font-size: 2rem;
}

.navbar-image {
  height: 80%;
  object-fit: cover;
  border-radius: 3.5rem;
}

.fa-bolt {
  margin-left: 0.5rem;
  font-size: 1.6rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: end;
  width: 70vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-title {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.nav-item {
  display: flex;
  align-items: center;
  height: 80px;
}

.nav-links {
  color: white;
  text-decoration: none;
  width: 100%;
  padding: 0.5rem 1rem;
}

.nav-links:hover {
  border-radius: 4px;
  transition: all 0.2s ease-out;
  box-shadow: 3px 5px 5px #1d1212;
  background-color: #2e2d40;
}

.fa-bars {
  color: rgb(255, 255, 255);
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

.nav-title-text {
  font-size: 2rem;
  font-weight: 300;
  background-image: linear-gradient(to right, #7f8fa6 0%, #a7aab3 100%);
  display: inline-block;
  -webkit-background-clip: text;
  color: transparent;
  letter-spacing: 0.2rem;
  margin-left: 3rem;
}

@media screen and (max-width: 700px) {
  .nav-menu {
    display: flex;
    flex-direction: column;
    opacity: 0;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 8rem;
    transform: translateX(100%);
    opacity: 0;
    transition: all 0.5s ease;
    margin-right: 0px;
  }

  .nav-menu.active {
    background: #487eb0;
    right: 0;
    opacity: 1;
    transform: translateX(20%);
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 80%;
  }

  .nav-links:hover {
    background-color: #1888ff;
    border-radius: 0;
  }

  .navbar-logo {
    font-size: 1.5rem;
  }

  .menu-icon {
    display: flex;
    justify-content: flex-end;
    align-content: flex-end;
    padding-right: 3rem;

    /*     position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    transition: all ease-in;*/
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-item {
    opacity: 0;
  }

  .nav-item.active {
    opacity: 1;
  }

  button {
    display: none;
  }
}
